.hero {
  margin-bottom: 375px;

  background: url('../img/hero/hero-bg.png') no-repeat;
  background-size: cover;
  background-position: center;
  background-color: var(--primary-color);
  height: 790px;

  @include medium-tablet {
    margin-bottom: 280px;
  }

  @media (max-width: 800px) {
    margin-bottom: 250px;
  }

  @include small-tablet {
    margin-bottom: 150px;
  }

  @media (max-width: 710px) {
    margin-bottom: 100px;
  }

  @include mobile {
    height: 600px;
  }

  @media (max-width: 460px) {
    margin-bottom: 70px;
  }

  @media (max-width: 370px) {
    margin-bottom: 40px;
  }

  &__content {
    padding-top: 150px;

    @include small-tablet {
      padding-top: 125px;
    }
  }

  &__title {
    margin: 0 auto;
    margin-bottom: 32px;
    max-width: 870px;
    font-size: 60px;

    font-weight: 800;
    line-height: 133%;
    text-align: center;
    letter-spacing: -0.03em;
    color: var(--light-color);

    @include medium-tablet {
      font-size: 52px;
      max-width: 820px;
      margin-bottom: 25px;
    }

    @include small-tablet {
      font-size: 44px;
      max-width: 705px;
      margin-bottom: 25px;
    }

    @include mobile {
      font-size: 28px;
      margin-bottom: 15px;
    }
  }

  &__descr {
    margin: 0 auto;
    margin-bottom: 56px;
    max-width: 650px;
    font-size: 20px;

    font-weight: 500;
    line-height: 150%;
    text-align: center;
    letter-spacing: -0.02em;
    color: var(--light-color);

    @include medium-tablet {
      font-size: 18px;
      margin-bottom: 45px;
    }

    @include small-tablet {
      margin-bottom: 35px;
    }

    @include mobile {
      font-size: 16px;
      margin-bottom: 25px;
    }
  }

  &__btns {
    display: flex;
    align-items: center;
    justify-content: center;

    margin: 0 auto;
    margin-bottom: 70px;

    @include medium-tablet {
      margin-bottom: 55px;
    }

    @include small-tablet {
      margin-bottom: 40px;
    }

    @include mobile {
      margin-bottom: 30px;
    }
  }

  &__btn {
    border-radius: 47px;
    margin-right: 50px;

    @include medium-tablet {
      margin-right: 40px;
    }

    @include small-tablet {
      margin-right: 25px;
    }

    @include mobile {
      margin-right: 15px;
    }
  }

  &__img {
    width: 100%;
    text-align: center;
  }
}

.play__btn {
  display: flex;
  align-items: center;
  gap: 8px;
  opacity: 1;
  transition: opacity 0.3s ease-in-out;

  &:hover {
    opacity: 0.9;
  }

  svg {
    @include mobile {
      width: 50px;
      height: 50px;
    }
  }
}

.video__descr {
  font-weight: 500;
  font-size: 17px;
  line-height: 176%;
  letter-spacing: -0.02em;
  color: var(--light-color);

  @include mobile {
    font-size: 12px;
    max-width: 50px;
    line-height: 140%;
  }
}
