.header {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  padding: 28px 0;
  width: 100%;
  z-index: 100;

  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__logo {
    position: relative;
    font-weight: 700;
    font-size: 20px;
    line-height: 27px;
    text-transform: uppercase;
    color: var(--light-color);

    &::before {
      position: absolute;
      content: "";
      top: -45px;
      left: -3px;
      width: 111px;
      height: 123px;
      background-image: url('../img/logo-decor.svg');
      background-repeat: no-repeat;
    }
  }

  &__btns {
    @include small-tablet {
      display: flex;
      flex-direction: column;
      align-items: center;
      margin-bottom: 50px;
    }
  }
}

.nav {
  display: inline-flex;
  align-items: center;
  justify-content: space-between;
  gap: 250px;

  @media (max-width: 1100px) {
    gap: 175px;
  }

  @media (max-width: 930px) {
    gap: 110px;
  }

  @media (max-width: 825px) {
    gap: 50px;
  }

  @include small-tablet {
    position: fixed;
    flex-direction: column;
    left: 0;
    top: 0;
    overflow-y: auto;
    width: 100%;
    height: 100vh;
    z-index: 500;
    background-color: var(--primary-color);
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
  }

  &__list {
    display: flex;
    align-items: center;

    @include small-tablet {
      flex-direction: column;
      padding-top: 120px;
      width: 100%;
      height: 100%;
      margin-bottom: 65px;
    }
  }

  &__item {
    position: relative;

    &:not(:last-child) {
      margin-right: 35px;

      @include medium-tablet {
        margin-right: 30px;
      }

      @include small-tablet {
        margin-right: 0;
        margin-bottom: 45px;
      }
    }
  }

  &__link {
    font-weight: 500;
    font-size: 14px;
    line-height: 19px;
    color: var(--light-color);
    opacity: 1;
    transition: opacity 0.3s ease-in-out;

    @include small-tablet {
      font-size: 20px;
      line-height: 28px;
    }

    &:hover {
      opacity: 0.7;
    }
  }

  &__btn {
    border: none;
    font-weight: 500;
    font-size: 14px;
    line-height: 19px;
    display: inline-flex;
    align-items: center;
    color: var(--light-color);
    background-color: transparent;
    cursor: pointer;
    opacity: 1;
    transition: opacity 0.3s ease-in-out;

    @include small-tablet {
      font-size: 20px;
      line-height: 28px;
    }

    &:hover {
      opacity: 0.7;
    }

    svg {
      margin-left: 8px;
    }
  }
}

.nav__btn--active {
  svg {
    transform: rotate(180deg);
  }
}

.dropdown {
  position: absolute;
  margin-top: 10px;
  left: 0;
  top: 100%;
  width: 150px;
  background-color: rgba(255, 255, 255, 0.9);
  border-radius: 15px;
  z-index: 1000;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
}

.dropdown--active {
  opacity: 1;
  visibility: visible;
}

.dropdown__item {
  padding: 12px 15px 15px 15px;

  &:not(:last-child) {
    padding: 12px 15px 5px 15px;
  }
}

.dropdown__link {
  display: block;
  width: 100%;
  font-weight: 500;
  font-size: 14px;
  line-height: 19px;
  color: var(--primary-color);
}

.burger {
  @include burger;
  z-index: 1000;

  @media (min-width: 768px) {
    display: none;
  }
}

.menu--active {
  opacity: 1;
  visibility: visible;
}
