.partners {
  padding-top: 50px;
  padding-bottom: 50px;

  @include small-tablet {
    padding-top: 50px;
    padding-bottom: 35px;
  }

  @include mobile {
    padding-bottom: 30px;
  }

  &__title {
    margin: 0;
    margin-bottom: 36px;
    font-weight: 500;
    font-size: 22px;
    line-height: 30px;
    text-align: center;
    color: #000000;

    @include small-tablet {
      padding-bottom: 25px;
      font-size: 20px;
    }

    @include mobile {
      padding-bottom: 5px;
      font-size: 17px;
    }
  }
}
