// colors
:root {
  // base
  --font-family: "Manrope", sans-serif;
  --font-weight: 500;
  --line-height: 1.5;
  --content-width: 1200px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // colors
  --primary-color: #3734A9;
  --orange-color: #FF7F5C;
  --text-color: #000000;
  --grey-color: #757095;
  --bg-color: #FAFBFF;
  --light-color: #ffffff;
}
