.blog {
  padding-top: 50px;
  padding-bottom: 155px;

  @include medium-tablet {
    padding-top: 40px;
    padding-bottom: 100px;
  }

  @include small-tablet {
    padding-bottom: 65px;
  }

  @include mobile {
    padding-top: 30px;
    padding-bottom: 50px;
  }

  &__title {
    margin: 0;
    margin-bottom: 72px;
    font-weight: 800;
    font-size: 50px;
    line-height: 132%;
    letter-spacing: -0.03em;
    color: var(--text-color);

    @include medium-tablet {
      font-size: 44px;
      margin-bottom: 40px;
    }

    @include small-tablet {
      font-size: 36px;
      margin-bottom: 30px;
    }

    @include mobile {
      font-size: 24px;
      margin-bottom: 20px;
    }
  }

  &__grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: 285px 436px;
    gap: 30px;

    @include medium-tablet {
      grid-template-columns: repeat(2, 1fr);
      grid-template-rows: 450px;
    }

    @include small-tablet {
      grid-template-columns: auto;
      grid-template-rows: auto;
    }

    @include mobile {
      gap: 20px;
    }
  }

  &__item {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    background-color: var(--light-color);

    &:nth-child(1) {
      grid-column: 1 / 4;
      flex-direction: row;

      @include medium-tablet {
        grid-column: 1 / 2;
        flex-direction: column;
      }

      /* Задал оригинальное значение для первой карточки. Она начинается с первого и заканчивается до четвертого */
    }
  }

  &__item-content {
    padding: 25px 40px 25px 32px;

    &:nth-child(1) {
      padding: 35px 40px 25px 32px;
      max-width: 575px;

      @include medium-tablet {
        padding: 25px 40px 25px 32px;
        max-width: none;
      }

      @include small-tablet {
        padding: 16px 20px 20px 20px;
      }
    }


  }

  &__item-category {
    margin-bottom: 7px;
    font-weight: 700;
    font-size: 16px;
    line-height: 225%;
    letter-spacing: 0.07em;
    text-transform: uppercase;
    color: var(--text-color);

    @include medium-tablet {
      font-size: 14px;
      margin-bottom: 4px;
    }

    @include mobile {
      font-size: 12px;
      margin-bottom: 0;
    }
  }

  &__item-title {
    margin: 0;
    font-weight: 700;
    font-size: 29px;
    line-height: 124%;
    letter-spacing: -0.02em;
    color: var(--text-color);

    @include medium-tablet {
      font-size: 24px;
    }

    @include mobile {
      font-size: 20px;
    }
  }

  &__item-img {

    @include medium-tablet {
      width: 100%;
      height: 250px;
    }

    @include small-tablet {
      height: 350px;
    }

    @include mobile {
      height: 230px;
    }

    @media (max-width: 450px) {
      height: 180px;
    }

    img {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__item-link {
    display: inline-block;
    margin: 0;
    margin-top: 70px;
    font-weight: 600;
    font-size: 16px;
    line-height: 100%;
    color: #1B1C31;
    transition: color 0.3s ease-in-out;

    svg {
      margin-left: 10px;
    }

    &:hover {
      color: var(--primary-color);
    }

    @include medium-tablet {
      display: none;
    }
  }
}
