.cta {
  padding-top: 72px;
  padding-bottom: 90px;
  background-color: #1B1C31;

  @include medium-tablet {
    padding-top: 50px;
    padding-bottom: 60px;
  }

  @include small-tablet {
    padding-bottom: 50px;
  }

  @include mobile {
    padding-top: 45px;
    padding-bottom: 40px;
  }

  &__content {
    text-align: center;
  }

  &__title {
    margin: 0;
    margin-bottom: 25px;
    font-weight: 500;
    font-size: 53px;
    line-height: 132%;
    text-align: center;
    letter-spacing: -0.02em;
    color: var(--light-color);

    @include medium-tablet {
      font-size: 44px;
      margin-bottom: 20px;
    }

    @include small-tablet {
      font-size: 36px;
    }

    @include mobile {
      font-size: 28px;
    }
  }

  &__btn {
    align-items: center;
    padding: 14px 30px 15px 23px;
    font-weight: 600;
    font-size: 14px;
    line-height: 19px;
    text-align: center;
    color: #121313;
    margin: 0 auto;

    @include mobile {
      padding: 12px 33px;
      line-height: 26px;
    }

    svg {
      margin-left: 12px;
    }
  }
}
