.how-it-work {
  padding-top: 95px;
  padding-bottom: 50px;

  @include medium-tablet {
    padding-top: 80px;
    padding-bottom: 40px;
  }

  @include small-tablet {
    padding-top: 65px;
    padding-bottom: 30px;
  }

  @include mobile {
    padding-top: 50px;
    padding-bottom: 50px;
  }

  &__content {
    margin-bottom: 75px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 38px 45px;
    background: var(--light-color);
    border: 1px solid #DEE1E6;
    border-radius: 20px;

    @include medium-tablet {
      align-items: start;
      padding: 35px 40px;
      margin-bottom: 55px;
    }

    @include small-tablet {
      margin-bottom: 35px;
      padding: 30px 30px;
    }

    @media (max-width: 650px) {
      flex-direction: column;
    }

    @include mobile {
      padding: 20px;
    }
  }

  &__title {
    margin: 0;
    margin-right: 30px;
    max-width: 330px;
    font-size: 34px;

    font-weight: 800;
    line-height: 135%;
    letter-spacing: -0.03em;
    color: var(--text-color);

    @include small-tablet {
      font-size: 28px;
      max-width: 240px;
    }

    @media (max-width: 650px) {
      margin-right: 0;
      margin-bottom: 30px;
    }

    @include mobile {
      font-size: 22px;
      margin-bottom: 20px;
    }
  }

  &__list {
    display: flex;
    align-items: center;
    justify-content: space-between;

    @media (max-width: 1200px) {
      flex: 0 1 50%;
      flex-direction: column;
      align-items: start;
    }

    @include mobile {
      flex: 1 1 100%;
    }
  }

  &__item {
    display: flex;
    align-items: center;

    &:not(:last-child) {
      margin-right: 35px;

      @media (max-width: 1200px) {
        margin-bottom: 25px;
      }

      @include small-tablet {
        margin-bottom: 20px;
      }

      @include mobile {
        margin-bottom: 15px;
      }
    }
  }

  &__item-img {
    margin-right: 18px;
    display: flex;
    align-items: center;
    justify-content: center;
    min-width: 60px;
    min-height: 60px;
    background: #EBECE8;
    border-radius: 5px;

    @include small-tablet {
      margin-right: 15px;
      min-width: 50px;
      min-height: 50px;
    }

    @include mobile {
      margin-right: 12px;
      min-width: 44px;
      min-height: 44px;
    }
  }

  &__item-text {
    margin: 0;
    max-width: 155px;
    font-size: 18px;
    font-weight: 600;
    line-height: 160%;
    letter-spacing: -0.02em;
    color: #64607D;

    @media (max-width: 650px) {
      max-width: 100%;
    }

    @include mobile {
      font-size: 16px;
      line-height: 140%;
    }
  }

  &__cards {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 30px;

    @include small-tablet {
      gap: 20px;
    }

    @media (max-width: 650px) {
      grid-template-columns: auto;
    }
  }

  &__card {
    padding: 40px;
    text-align: center;
    background-color: #FFFFFF;
    box-shadow: 0px 141.825px 113.46px rgba(0, 0, 0, 0.03), 0px 91.9235px 66.4476px rgba(0, 0, 0, 0.0227778), 0px 54.6288px 36.1391px rgba(0, 0, 0, 0.0182222), 0px 28.365px 18.4372px rgba(0, 0, 0, 0.015), 0px 11.5561px 9.24488px rgba(0, 0, 0, 0.0117778), 0px 2.62639px 4.46486px rgba(0, 0, 0, 0.00722222);
    border-radius: 20px;

    @include medium-tablet {
      padding: 35px;
    }

    @include small-tablet {
      padding: 25px;
    }

    @include mobile {
      padding: 20px;
    }
  }

  &__card-title {
    margin: 0 auto;
    margin-bottom: 20px;
    max-width: 450px;
    font-weight: 800;
    font-size: 42px;
    line-height: 119%;
    text-align: center;
    letter-spacing: -0.03em;
    color: var(--text-color);

    @include medium-tablet {
      font-size: 34px;
      margin-bottom: 15px;
    }

    @include small-tablet {
      font-size: 24px;
    }

    @include mobile {
      font-size: 20px;
      margin-bottom: 10px;
    }
  }

  &__card-descr {
    margin: 0 auto;
    margin-bottom: 36px;
    max-width: 470px;
    font-weight: 500;
    font-size: 16px;
    line-height: 180%;
    text-align: center;
    letter-spacing: -0.02em;
    color: #64607D;

    @include medium-tablet {
      margin-bottom: 25px;
    }

    @include small-tablet {
      line-height: 140%;
    }

    @include mobile {
      font-size: 14px;
      margin-bottom: 20px;
    }
  }

  .read-more__btn {
    margin: 0 auto;
    text-align: center;
    padding: 15px 53px;

    @include mobile {
      padding: 12px 33px;
    }
  }
}
