.footer {
  position: relative;
  padding-top: 50px;
  padding-bottom: 40px;
  background-color: #1B1C31;

  @include medium-tablet {
    padding-bottom: 30px;
  }

  @include small-tablet {
    padding-top: 35px;
    padding-bottom: 20px;
  }

  &::before {
    position: absolute;
    content: "";
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 1px;
    background-color: #404444;
  }

  &__content {
    display: flex;
    align-items: start;
    margin-bottom: 110px;

    @include medium-tablet {
      flex-direction: column;
      margin-bottom: 60px;
    }

    @include small-tablet {
      margin-bottom: 40px;
    }
  }

  &__info {
    flex: 0 1 50%;
    margin-top: 5px;

    @include medium-tablet {
      flex: 1 1 100%;
      margin-bottom: 40px;
    }

    @include small-tablet {
      margin-bottom: 30px;
    }

    &-logo {
      margin-bottom: 18px;
      font-weight: 700;
      font-size: 20px;
      line-height: 27px;
      text-transform: uppercase;
      color: var(--light-color);

      @include small-tablet {
        margin-bottom: 12px;
      }
    }

    &-descr {
      max-width: 470px;

      font-weight: 500;
      font-size: 16px;
      line-height: 162%;
      color: #FFFFFF;
      opacity: 0.4;

      @include medium-tablet {
        max-width: 100%;
      }

      @include small-tablet {
        font-size: 14px;
      }
    }
  }

  &__nav {
    display: flex;
    justify-content: space-between;

    @include mobile {
      display: none;
    }

    &-column {
      max-width: 180px;

      &:not(:last-child) {
        margin-right: 45px;

        @include medium-tablet {
          margin-right: 80px;
        }

        @media (max-width: 650px) {
          margin-right: 40px;
        }
      }
    }

    &-title {
      margin-bottom: 30px;
      font-weight: 700;
      font-size: 20px;
      line-height: 114%;
      color: var(--light-color);

      @media (max-width: 650px) {
        font-size: 17px;
        margin-bottom: 20px;
      }
    }

    &-item {
      &:not(:last-child) {
        margin-bottom: 18px;

        @media (max-width: 650px) {
          margin-bottom: 14px;
        }
      }
    }

    &-link {
      font-weight: 500;
      font-size: 16px;
      line-height: 150%;
      color: var(--light-color);
      opacity: 1;
      transition: opacity 0.3s ease-in-out;

      @media (max-width: 650px) {
        font-size: 14px;
      }

      &:hover {
        opacity: 0.7;
      }
    }
  }

  &__copy {
    position: relative;
    padding-top: 40px;
    display: flex;
    align-items: center;
    justify-content: space-between;

    @include medium-tablet {
      padding-top: 30px;
    }

    @include small-tablet {
      padding-top: 20px;
      flex-direction: column;
    }

    &::before {
      position: absolute;
      content: "";
      top: 0;
      left: 0;
      right: 0;
      width: 100%;
      height: 1px;
      background-color: #404444;
      opacity: 0.4;
    }

    &-left {
      text-align: center;
      font-weight: 500;
      font-size: 12px;
      line-height: 217%;
      color: var(--light-color);

      a {
        font-weight: 500;
        font-size: 12px;
        line-height: 217%;
        color: var(--light-color);
        opacity: 1;
        transition: opacity 0.3s ease-in-out;

        &:hover {
          opacity: 0.7;
        }
      }
    }

    &-right {
      font-weight: 500;
      font-size: 12px;
      line-height: 217%;
      text-align: right;
      color: var(--light-color);

      a {
        font-weight: 700;
        text-decoration: underline;
        color: var(--light-color);
        cursor: pointer;
        opacity: 1;
        transition: opacity 0.3s ease-in-out, text-decoration 0.3s ease-in-out;

        &:hover {
          text-decoration: none;
          opacity: 0.7;
        }
      }
    }
  }
}

// Аккордион

.accordion__list {
  width: 100%;
  @media (min-width: 570px) {
    display: none;
  }
}

.accordion__control {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 20px;
  border: solid 1px rgba(64, 68, 68, 0.4);
  width: 100%;

  background-color: transparent;
  cursor: pointer;
}

.accordion__control::-webkit-details-marker {
	display: none;
}

.accordion__content {
  max-height: 0;
  opacity: 0;
  overflow: hidden;
  padding: 0 20px;
  will-change: max-height;
  transition: all 0.3s ease-out;
  box-sizing: content-box;
}

.accordion__title {
  text-align: left;
  font-size: 17px;
  font-weight: 700;
  line-height: 114%;
  color: var(--light-color);
}

.accordion__icon {
  position: relative;
  display: inline-block;
  width: 30px;
  height: 30px;
  border-radius: 100%;
  background-color: var(--primary-color);
}

.accordion__icon::before {
  content: "";
  position: absolute;
  left: 50%;
  top: 50%;
  width: 50%;
  height: 1px;
  background-color: var(--light-color);
  transform: translate(-50%, -50%);
}

.accordion__icon::after {
  content: "";
  position: absolute;
  left: 50%;
  top: 50%;
  width: 1px;
  height: 50%;
  background-color: var(--light-color);
  transform: translate(-50%, -50%);
}

.accordion__icon {
  transition: transform 0.3s ease-out;
}

.open .accordion__icon {
  transform: rotate(45deg);
}

.open {
  .accordion__content {
    opacity: 1;
    padding: 20px;
    margin-bottom: 10px;
  }

  .accordion__title {
    color: var(--orange-color);
  }
}
