.about {
  padding-top: 145px;
  padding-bottom: 130px;
  background-color: var(--primary-color);

  @include medium-tablet {
    padding-top: 90px;
    padding-bottom: 90px;
  }

  @include small-tablet {
    padding-top: 65px;
    padding-bottom: 65px;
  }

  @include mobile {
    padding-top: 50px;
    padding-bottom: 50px;
  }

  &__heading {
    margin-bottom: 80px;

    display: flex;
    align-items: center;
    justify-content: space-between;

    @include medium-tablet {
      flex-direction: column;
      align-items: flex-start;
      margin-bottom: 50px;
    }

    @include mobile {
      margin-bottom: 30px;
    }
  }

  &__title {
    margin: 0;
    max-width: 825px;
    font-size: 45px;

    font-weight: 800;
    line-height: 120%;
    letter-spacing: -0.03em;
    color: #FFFFFF;

    @media (max-width: 1100px) {
      max-width: 700px;
    }

    @include medium-tablet {
      max-width: 100%;
      font-size: 40px;
      margin-bottom: 30px;
    }

    @include small-tablet {
      font-size: 34px;
    }

    @include mobile {
      font-size: 22px;
      margin-bottom: 20px;
    }
  }

  &__btn {
    display: inline-flex;
    padding: 18px 43px;
    border-radius: 10px;
    font-weight: 700;
    font-size: 18px;
    line-height: 167%;
    letter-spacing: -0.02em;
    color: var(--light-color);
    opacity: 1;
    transition: opacity 0.3s ease-in-out;

    @include mobile {
      padding: 12px 33px;
      font-size: 14px;
      line-height: 26px;
    }

    &:hover {
      opacity: 0.9;
    }

    &:active {
      box-shadow: 0px 4px 1px rgba(0, 0, 0, 0.15);
    }
  }

  &__column {
    display: flex;
    align-items: center;
    justify-content: space-between;

    @include medium-tablet {
      flex-direction: column;
      align-items: flex-start;
    }
  }

  &__list {
    flex-direction: column;
    margin-right: 30px;

    @include medium-tablet {
      margin-right: 0;
      margin-bottom: 60px;
    }

    @include small-tablet {
      margin-bottom: 40px;
    }

    @include mobile {
      margin-bottom: 30px;
    }
  }

  &__item {
    display: flex;
    align-items: start;

    &:not(:last-child) {
      margin-bottom: 35px;

      @include small-tablet {
        margin-bottom: 25px;
      }
    }

    @include medium-tablet {
      align-items: center;
    }

    @include small-tablet {
      align-items: start;
    }
  }

  &__item-number {
    margin-right: 28px;
    display: flex;
    text-align: center;
    justify-content: center;
    align-items: center;
    font-weight: 700;
    font-size: 46px;
    line-height: 70%;
    letter-spacing: -0.02em;
    color: #E8F2FF;
    width: 95px;
    height: 95px;
    border-radius: 100%;
    background-color: rgba($color: #1B1C31, $alpha: 0.2);

    @include small-tablet {
      margin-right: 20px;
      font-size: 34px;
      line-height: 64px;
      width: 64px;
      height: 64px;
    }

    @include mobile {
      margin-right: 15px;
      font-size: 20px;
      line-height: 50px;
      width: 50px;
      height: 50px;
    }
  }

  &__item-content {
    max-width: 312px;
    flex-direction: column;

    @include medium-tablet {
      max-width: 100%;
    }

    @include mobile {
      max-width: 225px;
    }
  }

  &__item-title {
    margin: 0;
    margin-bottom: 5px;
    font-size: 26px;

    font-weight: 700;
    line-height: 125%;
    letter-spacing: -0.02em;
    color: #E8F2FF;

    @include small-tablet {
      font-size: 20px;
      margin-bottom: 3px;
    }
  }

  &__item-descr {
    font-weight: 500;
    font-size: 19px;
    line-height: 171%;
    letter-spacing: -0.02em;
    color: #A0B3E9;

    @include small-tablet {
      font-size: 16px;
      line-height: 155%;
    }

    @include mobile {
      font-size: 14px;
    }
  }

  &__video {
    position: relative;
    width: 657px;
    height: 387px;

    @media (max-width: 1170px) {
      width: 600px;
    }

    @media (max-width: 1100px) {
      width: 550px;
    }

    @media (max-width: 1050px) {
      width: 500px;
    }

    @include medium-tablet {
      width: 657px;
    }

    @include small-tablet {
      width: 100%;
      height: 350px;
    }

    @include mobile {
      height: 270px;
    }

    @media (max-width: 450px) {
      height: 220px;
    }

    video {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }

    &::before {
      position: absolute;
      content: "";
      top: -40px;
      left: -44px;
      width: 148px;
      height: 148px;
      background-image: url('../img/about/decor-1.svg');
      background-repeat: no-repeat;

      @include medium-tablet {
        display: none;
      }
    }

    &::after {
      position: absolute;
      content: "";
      top: -30px;
      right: -33px;
      width: 88px;
      height: 90px;
      background-image: url('../img/about/decor-2.svg');
      background-repeat: no-repeat;
    }
  }

  &__play {
    position: absolute;
    left: 50%;
    top: 50%;
    width: 94px;
    height: 94px;
    transform: translate(-50%, -50%);

    @include small-tablet {
      width: 85px;
      height: 85px;
    }

    @include mobile {
      width: 65px;
      height: 65px;
    }

    svg {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__play--hidden {
    display: none;
  }
}
