.btn {
  display: inline-flex;
  padding: 15px 38px;
  border-radius: 10px;
  font-weight: 500;
  font-size: 16px;
  line-height: 30px;
  letter-spacing: -0.02em;
  color: var(--light-color);
  opacity: 1;
  transition: opacity 0.3s ease-in-out;

  @include mobile {
    padding: 12px 33px;
    font-size: 14px;
    line-height: 26px;
  }

  &:hover {
    opacity: 0.9;
  }

  &:active {
    box-shadow: 0px 4px 1px rgba(0, 0, 0, 0.15);
  }
}

.btn-orange {
  background-color: var(--orange-color);
}

.btn-blue {
  background-color: var(--primary-color);
}

.sign-in__btn {
  margin-right: 50px;
  font-weight: 500;
  font-size: 14px;
  line-height: 19px;
  color: var(--light-color);
  transition: opacity 0.3s ease-in-out;

  @include medium-tablet {
    margin-right: 35px;
  }

  @include small-tablet {
    margin: 0;
    margin-bottom: 30px;
    font-size: 18px;
    line-height: 24px;
  }

  &:hover {
    opacity: 0.7;
  }

  &:active {
    box-shadow: 0px 4px 1px rgba(0, 0, 0, 0.15);
  }
}

.header__btn {
  margin: 0;
  padding: 16px 36px;
  font-weight: 500;
  font-size: 14px;
  line-height: 19px;
  color: var(--light-color);
  transition: opacity 0.3s ease-in-out;

  @include medium-tablet {
    padding: 14px 30px;
  }

  @include small-tablet {
    padding: 16px 36px;
  }

  @include mobile {
    padding: 12px 33px;
    font-size: 14px;
    line-height: 26px;
  }

  &:hover {
    opacity: 0.7;
  }

  &:active {
    box-shadow: 0px 4px 1px rgba(0, 0, 0, 0.15);
  }
}
