.feature {
  padding-top: 50px;
  padding-bottom: 110px;

  @include medium-tablet {
    padding-bottom: 80px;
  }

  @include small-tablet {
    padding-bottom: 65px;
  }

  @include mobile {
    padding-top: 40px;
    padding-bottom: 50px;
  }

  &__subtitle {
    margin-bottom: 25px;

    font-weight: 800;
    font-size: 18px;
    line-height: 25px;
    text-transform: uppercase;
    color: var(--primary-color);

    @include medium-tablet {
      margin-bottom: 15px;
    }

    @include small-tablet {
      margin-bottom: 20px;
      font-size: 16px;
      line-height: 22px;
    }

    @include mobile {
      margin-bottom: 10px;
      font-size: 14px;
      line-height: 18px;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 25px;
    font-size: 50px;

    font-weight: 700;
    line-height: 112%;
    letter-spacing: -0.03em;
    color: var(--text-color);

    @include medium-tablet {
      font-size: 46px;
      max-width: 100%;
      margin-bottom: 20px;
    }

    @include small-tablet {
      font-size: 38px;
    }

    @include mobile {
      font-size: 24px;
      margin-bottom: 10px;
    }
  }

  &__descr {
    margin: 0;
    margin-bottom: 25px;
    max-width: 512px;
    font-size: 18px;

    font-weight: 500;
    line-height: 144%;
    letter-spacing: -0.02em;
    color: var(--grey-color);

    @include medium-tablet {
      max-width: 100%;
      margin-bottom: 20px;
    }

    @include small-tablet {
      font-size: 16px;
    }
  }

  &__column {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 30px;

    @include medium-tablet {
      flex-direction: column;

      &:not(:last-child) {
        flex-direction: column-reverse;
      }
    }

    &:not(:last-child) {
      margin-bottom: 80px;

      @include small-tablet {
        margin-bottom: 60px;
      }
    }

    &-content {
      max-width: 580px;

      @include medium-tablet {
        max-width: 100%;
      }
    }

    &-img {
      display: block;
      img {
        width: 100%;
        height: 100%;
      }
    }
  }
}
